import { useCallback, useState } from 'react';
import { exportCSVFile } from '../utils/utils.js';
export const downloadCommunityMemebers = async (communityId, size, getEntityCallback, level)=>{
    const response = await getEntityCallback(undefined, {
        loadCount: size,
        communityId: communityId
    });
    const filename = `c${communityId}_level${level}`;
    if (response) {
        exportCSVFile(response, filename);
    } else {
        throw Error('DataProvider undefined, unable to download CSV');
    }
};
export function useCommunityDownload(communityId, size, getEntityCallback, level) {
    const [isLoading, setIsLoading] = useState(false);
    const handleDownloadClick = useCallback((event)=>{
        event.stopPropagation();
        setIsLoading(true);
        console.log(`initiated download of level: ${level} community: ${level}`);
        const promise = downloadCommunityMemebers(communityId, size, getEntityCallback, level);
        promise.catch((reason)=>{
            console.error(reason);
        });
        promise.finally(()=>setIsLoading(false));
    }, [
        communityId,
        setIsLoading,
        getEntityCallback,
        level,
        size
    ]);
    return [
        handleDownloadClick,
        isLoading
    ];
}
